import {fetch} from '@/utils/fetch';

/**
 * 经理自助接口
 * @param {*}  params
 */
export const managerPersonal = (params) => fetch({
  url: 'api/hrmanagerself/query',
  method: 'post',
  data: params
});

/**
 * 经理自助下属级别接口
 * @param {*}  params
 */
export const managerLower = (params) => fetch({
  url: 'api/hrmanagerself/getLevel',
  method: 'post',
  data: params
});

/**
 * 经理自助搜索接口
 * @param {*}  params
 */
export const managerSearch = (params) => fetch({
  url: 'api/hrmanagerself/search',
  method: 'post',
  data: params
});



